.lds-ring {
  display: inline-block;
  position: relative;
  height: 30px;
  width: 30px;
}

.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 20px;
  height: 20px;
  margin: 0px;
  border: 4px solid #000;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #000 transparent transparent transparent;
}

.lds-ring div:nth-child(1) {
  animation-delay: -0.45s;
}

.lds-ring div:nth-child(2) {
  animation-delay: -0.3s;
}

.lds-ring div:nth-child(3) {
  animation-delay: -0.15s;
}

@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.spinner {
  width: 100%;
  text-align: center;
}

.spinner > div {
  width: 10px;
  height: 10px;
  background-color: white;
  margin-right: 1px;
  border-radius: 100%;
  display: inline-block;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}

.spinner .bounce1 {
  animation-delay: -0.32s;
}

.spinner .bounce2 {
  animation-delay: -0.16s;
}

@keyframes sk-bouncedelay {
  0%,
  80%,
  100% {
    transform: scale(0);
  }

  40% {
    transform: scale(0.7);
  }
}

.correct-position {
  position: "absolute";
  text-align: "center";
  transform: translate(-50%, -50%);
}

/***** BACKGROUND COLOR FIX FOR IE *****/

.background-white-ie-fix {
  background: white;
}

/***** MOBILE FONTS *****/

.heading-font {
  font-family: "Roboto Slab", serif;
  font-weight: 700;
  font-size: 2rem;
  line-height: 1.2;
  color: white;
}

.confirmation-font {
  font-family: "Roboto Slab", serif;
  font-weight: 600;
  font-size: 1.25rem;
  color: white;
}

.body-font {
  font-family: "Roboto", serif;
  font-weight: 400;
  font-size: 1rem;
}

/***** DESKTOP FONTS *****/

@media (min-width: 766px) {
  .heading-font {
    font-size: 2.5rem;
  }

  .confirmation-font {
    font-size: 1.5rem;
  }

  .body-font {
    font-size: 1.25rem;
  }
}

